.assess-container {
  display: flex;
  flex-wrap: wrap;
  .title {
    width: 100%;
    margin-top: 36px;
    font-size: 60px;
    color: #48f0ff;
    text-align: center;
  }
  .score-board-container {
    width: 430px;
    .ranking {
      margin: 140px 0 0 42px;
      .value {
        width: 400px;
        height: 88px;
        line-height: 52px;
        margin-top: 52px;
        background: url(./images/plate.png) no-repeat center / 100% 100%;
        font-size: 60px;
        color: #48f0ff;
        text-align: center;
        span {
          color: #FFC80B;
        }
      }
    }

    .score {      
      margin: 36px 0 0 42px;
      .score-list {
        width: 100%;
        .score-item {
          display: flex;
          align-items: center;
          margin-top: 24px;
          span:nth-of-type(1) {
            display: block;
            width: 128px;
            height: 60px;
            line-height: 60px;
            background: url(./images/titleBg.png) no-repeat center / 100% 100%;
            font-size: 32px;
            color: #48f0ff;
            text-align: center;
          }
          span:nth-of-type(2) {
            display: block;
            flex: 1;
            height: 66px;
            line-height: 66px;
            margin-left: 32px;            
            background: url(./images/valueBg.png) no-repeat center / 100% 100%;
            font-size: 42px;
            color: #FFC80B;
            text-align: center;
          }
        }
        .score-item:nth-of-type(2) {
          margin-top: 32px;
        }
      }
    }
  }
}

.value-title {
  width: 100%;
  display: flex;
  align-items: center;
  span {
    display: block;
    width: 30px;
    height: 30px;
    background: url(./images/point.png) no-repeat center / 100% 100%;
  }
  p {
    font-size: 36px;
    color: #48f0ff;
    margin-left: 8px;
  }
}

#assessChart {
  margin-top: 80px;
  margin-left: 120px;
}


