.monitor-container {
  margin-top: 66px;
  .title {
    width: 92%;
    margin: 0 auto;
    font-size: 60px;
    color: #48f0ff;
    text-align: center;
    background: url(data:image/png;base64,) no-repeat center / contain;
  }
  .transfer-container {    
    margin: 8px auto 0 auto;
  }
  .text-container {
    display: flex;
    justify-content: space-between;
    width: 1112px;
    margin: 0 auto;
    font-size: 32px;
    color: #00F6FF;
  }  
}

.plate-container {
  width: 100%;
  display: flex;
  justify-content: space-around;
  .plate-item {
    width: 370px;
    height: 144px;
    p:nth-child(1) {
      font-size: 30px;
      color: #00F6FF;
      text-align: center;
      margin-top: 88px;
    }
    p:nth-child(2) {
      font-size: 36px;
      color: #FF910F;
      text-align: center;
      font-family: digifacewide;
      margin-top: 12px;
    }
  }
  .plate-item:nth-child(1) {
    background: url(./images/plate1.png) no-repeat center / 100% 100%;
  }
  .plate-item:nth-child(2) {
    background: url(./images/plate2.png) no-repeat center / 100% 100%;
  }
  .plate-item:nth-child(3) {
    background: url(./images/plate3.png) no-repeat center / 100% 100%;
  }
}


